<template>
    <h1 class="h1 text-center">Цены на криптовалюты!</h1>
    <table class="table table-striped mx-auto w-50 mt-3">
        <thead>
            <tr class="table-warning">
            <th>Название</th>
            <th>Цена USD</th>
            </tr>
        </thead>
  
        <tbody>
            <tr v-if="crypto.data.length === 0">
                <td colspan="2">Загрузка данных...</td>
            </tr>
            <tr class="table-info" v-else v-for="item in crypto.data" :key="item.rank">
                <td>{{ item.symbol }}</td>
                <td>$ {{ item.priceUsd }}</td>
            </tr>
        </tbody>
  
    </table>
  </template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            crypto: { data: [] }
        }
    },
    async mounted() {
        await this.getCrypto();
    },

    methods: {
        async getCrypto() {
            try {
                const response = await axios.get('https://api.coincap.io/v2/assets');
                this.crypto = response.data;
            } catch (error) {
                console.log(error);
            }   
        } 
    }
}
</script>